.accordian-container {
  width: 200px;
  /* background-color: white; */
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  font-size: 14px;
  font-weight: 600;
  z-index: 1;
  position: relative;

  /* height: 50px; */
  /* z-index: 20; */
}
.accordian-container:focus-visible {
  background-color: red;
}
.accordian-container.dark {
  background-color: hsl(209, 23%, 22%);
}
.accordian-controls {
  width: 200px;
  /* background-color: white; */
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 50px;
  cursor: pointer;
  border-radius: 5px;
  position: relative;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.accordian-text {
  height: 50px;
  font-size: 15px;
  /* width: 100px; */
  /* color: white; */
  margin: auto 15px;
  line-height: 50px;
}
.accordian-details {
  width: 200px;
  background-color: white;
  position: absolute;
  top: 50px;
  font-weight: 600;
  margin-top: 5px;
  display: none;
  border-radius: 5px;
  z-index: 20;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.accordian-details > ul {
  list-style: none;
  /* margin: 5px 15px; */
  display: flex;
  width: 200px;
  flex-direction: column;
}
.accordian-details > ul > li:hover {
  background-color: rgb(167, 164, 164);
  cursor: pointer;
}
.accordian-details > ul > li {
  width: 100%;
  padding: 5px 0px 5px 15px;
}

.accordian-icon {
  width: 50px;
  height: 50px;
  /* background-color: white; */
  padding: 20px;
  margin-right: 10px;
  /* display: none; */
}
.accordian-icon:hover {
  cursor: pointer;
}
.rot {
  transform: rotate(180deg);
}
