.main-section {
  display: flex;
  background-color: hsl(0, 0%, 98%);
  flex: 1 0;
  /* width: 100%; */
  max-width: 100vw;
  /* min-width: 100vw; */
  /* flex-wrap: wrap; */
  /* justify-self: auto; */
  justify-content: space-evenly;
  flex-direction: column;
  padding: 30px 100px;
}
.main-section.dark {
  background-color: hsl(207, 26%, 17%);
}
@media screen and (max-width: 1200px) {
  .main-section {
    padding: 50px;
  }
}

@media screen and (max-width: 800px) {
  .main-section {
    padding: 50px;
    padding-left: 10%;
  }
}
@media screen and (max-width: 580px) {
  .main-section {
    /* padding: 15px; */
    padding-left: 10%;
  }
}
@media screen and (max-width: 580px) {
  .main-section {
    /* padding: 15px; */
    padding-left: 15px;
  }
}
