.search-bar-container {
  width: 100%;
  /* height: 30px; */
  display: flex;
  /* background-color: hsl(209, 23%, 22%); */

  justify-content: space-between;
  z-index: 1;
  /* border-radius: 10px; */
}

.search-bar-container.dark {
  background-color: hsl(207, 26%, 17%);
}

.search-bar {
  width: 70%;
  /* min-width: 250px; */
  height: 50px;
  display: flex;
  flex-direction: row;
  border-radius: 5px;
  /* background-color: hsl(209, 23%, 22%); */

  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 0 5px;
}

.search-bar.dark {
  background-color: hsl(209, 23%, 22%);
  box-shadow: none;
}
.search-icon {
  width: 50px;
  height: 50px;
  /* background-color: white; */
  padding: 15px;
  /* background-color: hsl(209, 23%, 22%); */

  border-radius: 4px;
  /* margin-left: 10px; */
}
.search-icon.dark {
  background-color: hsl(209, 23%, 22%);
}
.search-text {
  height: 50px;
  width: 100%;
  border: grey;
  /* background-color: hsl(209, 23%, 22%); */

  font-size: 14px;
  border-radius: 4px;
  /* margin-right: 10px; */
  /* box-shadow: 2, 4; */
}
.search-text.dark {
  background-color: hsl(209, 23%, 22%);
}
.search-text:focus {
  outline: none;
}
@media screen and (max-width: 1200px) {
  .search-bar-container {
    /* width: 100%; */
    /* height: 30px; */
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    /* border-radius: 10px; */
  }
  .search-bar {
    width: 50%;
    border-radius: 5px;
    padding: 0 5px;
    /* margin: 10px; */
  }
}
@media screen and (max-width: 800px) {
  .search-bar-container {
    /* width: 100%; */
    /* height: 30px; */
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    /* border-radius: 10px; */
  }
  .search-bar {
    width: 50%;
    border-radius: 5px;
    padding: 0 5px;
    /* margin: 10px; */
  }
}

@media screen and (max-width: 580px) {
  .search-bar-container {
    display: flex;
    flex-direction: column;
  }
  .search-bar {
    width: 95%;
    padding: 0 5px;
    margin: 10px;
  }
}
