.dark-background1 {
  background-color: hsl(207, 26%, 17%);
  color: hsl(0, 0%, 100%);
  stroke: hsl(0, 0%, 100%);
}
.dark-background2 {
  background-color: hsl(209, 23%, 22%);
  color: hsl(0, 0%, 100%);
  stroke: hsl(0, 0%, 100%);
  box-shadow: none;
}
.dark-font1 {
  color: hsl(0, 0%, 100%);
}
.dark-font2 {
  color: hsl(0, 0%, 52%);
}
.no-shadow {
  box-shadow: none;
}
