.card-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex: 1 0;
  width: 100%;
  /* justify-content: center; */
}
.card {
  width: 250px;
  height: 300px;
  background-color: white;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  margin: 20px 10px;
  max-width: 100%;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
.card.dark {
  background-color: hsl(209, 23%, 22%);
  box-shadow: none;
  color: white;
}
.card img {
  width: 250px;
  height: 150px;
  /* border-top-left-radius: 10px; */
  border-radius: 5px 5px 0 0;
}
.country-name {
  font-size: 16px;
  font-weight: 800;
  padding: 20px;
  /* text-align: center; */
}
.card span:nth-child(2) {
  color: hsl(0, 0%, 52%);
}

.country-details {
  font-family: "Nunito Sans", sans-serif;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  font-weight: 500;
  padding-left: 20px;
}
.country-details > div span:first-child {
  font-weight: 600;
  margin-right: 5px;
  /* color: red; */
}
.card:hover {
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
@media screen and (max-width: 1200px) {
  .card {
    width: 250px;
    height: 300px;
  }
  .card img {
    width: 250px;
    height: 150px;
  }
}
@media screen and (max-width: 800px) {
  .card {
    width: 220px;
    height: 300px;
  }
  .card img {
    width: 220px;
    height: 150px;
  }
}

@media screen and (max-width: 580px) {
  .card {
    width: 250px;
    height: 300px;
  }
  .card img {
    width: 250px;
    height: 150px;
  }
}
@media screen and (max-width: 375px) {
  .card {
    width: 250px;
    height: 300px;
    margin-left: auto;
  }
  .card img {
    width: 250px;
    height: 150px;
  }
}
