.theme-button {
  width: 150px;
  font-size: 16px;
  height: 50px;
  display: flex;
  align-items: baseline;
  background-color: white;
  justify-content: center;
  font-weight: 600;
  border-radius: 5px;
  position: absolute;
  right: 50px;
  /* box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px; */
}
.theme-button.dark {
  color: hsl(0, 0%, 100%);
  background-color: hsl(209, 23%, 22%);
}
.theme-button:hover {
  cursor: pointer;
}
.theme-button:active {
  transform: translateY(2px);
}
.moon-icon {
  width: 15px;
  height: 15px;
  margin: auto 5px;
}
/* .moon-icon.dark {
  color: #ffffff;
} */
.theme-button-text {
  font-size: 15px;
  line-height: 50px;
  margin-right: 5px;
  white-space: nowrap;
  /* height: 30px; */
}

@media screen and (max-width: 600px) {
  .theme-button {
    right: 50px;
    width: 50px;
  }
}
