header {
  min-height: 50px;
  border-bottom: 1px;
  color: black;
  font-size: 30px;
  padding-left: 40px;
  display: flex;
  flex-direction: row;
  font-weight: 600;
}
/* .header.dark {
  background-color: hsl(209, 23%, 22%);
  color: hsl(0, 0%, 100%);
} */
@media screen and (max-width: 600px) {
  .header {
    font-size: 18px;
    padding-left: 15px;
  }
  .header .heading {
    margin: auto 0px;
    font-weight: 800;
    position: absolute;
    left: 30px;
    /* height: 50px; */
    line-height: 50px;
    /* align-self: center; */
    /* line-height: 50px; */
  }
}

@media screen and (max-width: 375px) {
  .header {
    font-size: 16px;
    padding-left: 15px;
  }
  .header .heading {
    margin: auto 0px;
    font-weight: 800;
    /* line-height: 50px; */
  }
}
