.details-section {
  /* border: solid 1px; */
  display: flex;
  padding: 50px 150px;
  background-color: hsl(0, 0%, 98%);
  flex: 1 0;
  /* width: 100%; */
  max-width: 100vw;
  min-width: 100vw;
  /* flex-wrap: wrap; */
  /* justify-self: auto; */
  /* justify-content: center; */
  flex-direction: column;
  /* padding-left: 100px; */
}
.details-section.dark {
  background-color: hsl(207, 26%, 17%);
  color: hsl(0, 0%, 100%);
}
.back-button {
  width: 100px;
  font-size: 16px;
  height: 30px;
  display: flex;
  align-items: baseline;
  /* background-color: white; */
  justify-content: center;
  font-weight: 600;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.back-button.dark {
  background-color: hsl(209, 23%, 22%);
  color: hsl(0, 0%, 100%);
}
.back-button:hover {
  cursor: pointer;
}
.back-button:active {
  transform: translateY(2px);
}
.back-arrow {
  width: 15px;
  height: 15px;
  margin: auto 5px;
}

.back-arrow.dark {
  color: white;
  stroke: white;
}
.back-button-text {
  font-size: 15px;
  line-height: 30px;
  margin-right: 5px;
}

.info {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 100px 0px;
}
.country-detail-image {
  width: calc(100vw - 100px);
  max-width: 450px;
  align-self: flex-start;
  height: 250px;
  margin: 0px 20px 20px 0px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.country-info {
  flex: 1;
  display: flex;
  flex-wrap: no-wrap;
  flex-direction: column;
  margin-right: 50px;
}
.country-title {
  padding-left: 15px;
}
.g-1 {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.g-11 {
  display: flex;
  flex-direction: column;
  margin: 10px 0 10px 10px;
}

.g-111 {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.g-111 > span {
  margin: 5px;
}
.g-12 {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-left: 15px;
}
.info-title {
  font-weight: 700;
  margin: auto 10px auto 0px;
  white-space: nowrap;
}
.info-text {
  /* white-space: nowrap; */
  color: hsl(0, 0%, 52%);
}
.border-countries-group {
  margin: 10px 0px;
  display: flex;
  flex-wrap: wrap;
}
.border-country {
  height: 30px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 5px;
  padding: 5px;
  margin: 5px;
  white-space: nowrap;
  /* flex: 1; */
}

@media screen and (max-width: 1200px) {
  .details-section {
    padding: 50px 0 0 100px;
  }
  .country-detail-image {
    width: calc(100vw - 200px);

    margin: 0px 20px 20px 0;
    height: 250px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
}
@media screen and (max-width: 800px) {
  .details-section {
    padding: 50px;
  }
  .country-detail-image {
    width: calc(100vw - 100px);

    margin: 20px 0px;
    height: 250px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
}
@media screen and (max-width: 450px) {
  .details-section {
    padding: 20px;
  }
  .country-detail-image {
    width: calc(100vw - 40px);
    /* min-width: 400px; */
    /* max-width: calc(100vw- 20px); */
    margin: 20px 0px;
    height: 250px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
}
